* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar-top {
  margin-top: 20px;
  padding-bottom: 20px;
  margin-left: -7.5%;
  border-bottom: 0.5px solid #000000;
  font-family: "DM Sans", sans-serif;
}
.navbar-top .navbar .open-close {
  padding: none;
  margin: none;
}
.navbar-top .navbar .open-close .open-close-btn {
  display: none;
}
.navbar-top .navbar .logo {
  display: none;
}
.navbar-top .navbar .navbar-nav .navbar-list {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.navbar-top .navbar .navbar-nav .navbar-list .navbar-item {
  list-style-type: none;
}
.navbar-top .navbar .navbar-nav .navbar-list .navbar-item .navbar-link {
  text-decoration: none;
  color: black;
  transition: all 350ms ease;
}
.navbar-top .navbar .navbar-nav .navbar-list .navbar-item .navbar-link.navbar-link-logo {
  cursor: default;
}
.navbar-top .navbar .navbar-nav .navbar-list .navbar-item .navbar-link.navbar-link-logo img {
  width: 40px;
  height: 40px;
}
.navbar-top .navbar .navbar-nav .navbar-list .navbar-item .navbar-link.navbar-link-logo:hover {
  color: black;
}
.navbar-top .navbar .navbar-nav .navbar-list .navbar-item .navbar-link.navbar-link-logo.responsive {
  display: none;
}
.navbar-top .navbar .navbar-nav .navbar-list .navbar-item .navbar-link:hover {
  color: aquamarine;
}

@media (max-width: 992px) {
  .navbar-top .navbar .open-close {
    position: absolute;
    right: 25px;
    top: 25px;
    cursor: pointer;
  }
  .navbar-top .navbar .open-close .open-close-btn {
    width: 40px;
  }
  .navbar-top .navbar .open-close .open-close-btn.active {
    display: block;
  }
  .navbar-top .navbar .logo {
    display: block;
    text-decoration: none;
    color: black;
    cursor: default;
    text-align: center;
  }
  .navbar-top .navbar .logo img {
    width: 40px;
    height: auto;
  }
  .navbar-top .navbar .navbar-nav {
    display: none;
  }
  .navbar-top .navbar .navbar-nav.active {
    display: block;
  }
  .navbar-top .navbar .navbar-nav .navbar-list {
    flex-direction: column;
    gap: 5px;
  }
  .navbar-top .navbar .navbar-nav .navbar-list .navbar-item .navbar-link {
    margin: 10px;
  }
  .navbar-top .navbar .navbar-nav .navbar-list .navbar-item .navbar-link.navbar-link-logo {
    display: none !important;
    z-index: -1;
    padding: 0px;
  }
  .navbar-top .navbar .navbar-nav .navbar-list .navbar-item .navbar-link.navbar-link-logo.responsive {
    display: block;
  }
}